}
.focus {
    background-color: var(--secondary-color) !important;
    color: white !important;
}
.bet-totals>.btn-reset {
    cursor: pointer;
    background-color: var(--secondary-color);
    float: left;
    padding: 25px 10px;
    margin: 5px;
    text-align: center;
    color: #f5f5f5;
