}

.payingamount {
    width: 100%;
    text-align: right;
    border: 1px solid grey;
}
.payingamount:focus {
    border: 1px solid black !important;
}

.place-bet p {
    border: 1px solid #333;
    margin: 5px;
    padding: 5px 10px;
    background: #fff;
    border-radius: 3px;
    float: left;
    color: #000;
}

.btn-place-bet {
    cursor: pointer;
    width: 98%;
    background-color: var(--primary-color) !important;
    text-align: center;
    color: white !important;
}
.btn-place-bet:hover {
    color: #000 !important;
}
.instead {
    padding-right: 3px;
}
.cancel {
    background-color: #971f1e !important;
}
.menu-header {
    background-color: #d69309;
    padding: 9px 8px 8px 12px;
    border-radius: 5px 5px 0 0;
    font-size: 17px;
    color: #f5f5f5;
