}

.menu-content {
    overflow: auto;
    height: 660px;
}

.sportstypes {
    color: #1c1c1c;
    font-weight: 700;
    border-bottom: 1px solid #c1c1c1;
    background: linear-gradient(180deg, #f1f1f1 0, #d7d7d7);
    font-size: 16px;
    text-decoration: none;
    display: block;
    outline: 0;
}

.sportstypes ul {
    padding-left: 0px;
    margin-bottom: 0px;
}

.sportstypes p {
    cursor: pointer;
}

.sportstypes .collapsed {
    cursor: pointer;
}

.collapsed {
    color: #d69309;
    cursor: pointer;
}

.first-lists {
    padding: 11px 10px 11px 10px;
}

.lists {
    background-color: #007219;
    color: #f5f5f5;
    border-bottom: solid 1px #f5f5f5;
    padding: 10px 10px 10px 10px;
}

.last-lists {
    padding-left: 31px;
}

.caret-icon {
    height: 5px;
    background-color: black;
}

.leftmenu-icon {
    height: 14px;
    margin-left: 3px;
    margin-right: 10px;
}

.sport-icon {
    height: 20px;
    width: 20px;
    margin-right: 7px;
}
.open {
    width: 60px !important;
    max-height: 25px;
    float: left;
    display: inline-block;
    padding: 10px 2px !important;
    transition: width, .5s linear;
}
.match-group {
    font-size: 13px;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    margin-bottom: 40px;
}

.match-group>.table-header {
    position: relative;
    background-repeat: no-repeat;
    background-size: 100% auto;
    border-bottom: 0 none;
    background-image: linear-gradient(180deg, #f3f3f3 0, #ddd);
    box-shadow: 0 6px 3px -3px #777;
}

.match-group>.table-header>.table-title {
    margin: 10px;
    float: left;
    line-height: 13px;
}

.match-group>.table-content {
    margin-top: 30px;
}

.match-group>.table-content>.event-list {
    width: 100%;
}

.match-group>.table-content>.event-list>.event {
    border-left: none transparent;
    border-top: 1px solid #c1c1c1;
    border-bottom: none transparent;
    width: 100%;
    float: left;
}
.match-group>.table-content>.event-list>.event:hover {
    background: #ddd;
}
.match-group>.table-content>.event-list>.event>div {
    align-items: center;
    padding: 0px 17px;
    float: left;
    border-left: 1px solid #ccc;
}

.match-group>.table-content>.event:nth-child(odd) {
    background-color: #efefef;
}

.match-group>.table-content>.event-list>.event>.date {
    border-left: 0px;
    float: left;
    /* max-width: 6%; */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 100%;
    text-align: center;
}

.match-group>.table-content>.event-list>.event {
    padding: 0px;
}
.match-group>.table-content>.event-list>.event>.date p, .match-group>.table-content>.event-list>.event>.time p, .team-name>p {
    padding: 8px 0;
}
.match-group>.table-content>.event-list>.event>.time {
    float: left;
    color: #338d2d;
}
.time p {
    font-weight: 700;
}
.match-group>.table-content>.event-list>.event>.detail-caret {
    width: 9px;
    height: 35px;
    cursor: default;
    color: #fff;
    padding-left: 2px;
    padding-right: 2px;
    background-color: #338d2d;
}
/* .match-group>.table-content>.event-list>.event>.team-name {
    margin: 10px 0px;
} */
.match-group>.table-content>.event-list>.event>.more-odds {
    width: 44px;
    max-width: 5%;
    float: right;
    padding: 5px 10px;
}

.match-group>.table-content>.event-list>.event>.more-odds>p {
    padding: 4px 6px;
    cursor: pointer;
    background: linear-gradient(180deg, #fefefe, #dadada);
    border: 1px solid #5a5a5a;
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px #f5f5f5;
    text-align: center;
    text-decoration: none;
    text-shadow: 0 1px 0 #fafafa;
    padding: 2px 6px;
}
.match-group>.table-content>.event-list>.event>.odds {
    text-align: center;
    float: right;
    padding: 5px;
    max-width: 25%;
    /* border-left: 1px solid #ccc; */
}
.match-group>.table-content>.event-list>.event>.odds>.treshold {
    padding: 2px;
    padding-top: 5px;
    margin-right: 6px;
    width: 30px;
    float: left;
    color: #d69309;
    font-weight: 700 !important;
}
.match-group>.table-content>.event-list>.event>.odds>.odd {
    padding: 2px;
    margin-right: 5px;
    width: 60px;
    float: left;
    color: #000;
    cursor: pointer;
    background: linear-gradient(180deg, #fefefe, #dadada);
    border: 1px solid #5a5a5a;
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px #f5f5f5;
    text-align: center;
    text-decoration: none;
    text-shadow: 0 1px 0 #fafafa;
    display: flex;
    justify-content: space-around;
}
.match-group>.table-content>.event-list>.event>.odds>.odd:hover {
    box-shadow: 0 0 1px 1px #777;
}
.match-group>.table-content>.event-list>.event>.odds>.odd.selected {
    background: #f69f16;
    color: #fff;
    box-shadow: none;
    border-bottom: none;
}
.detail-caret-right {
    background-image: url(/static/media/caret-right-solid.fc8ddd8c.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 6px;
}

.detail-caret-left {
    background-image: url(/static/media/caret-left-solid.22dd6461.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 6px;
}

.detail-caret-right:hover {
    cursor: pointer !important;
    text-align: right;
    -webkit-animation-name: stretchRight;
            animation-name: stretchRight;
    -webkit-animation-duration: 1.2s;
            animation-duration: 1.2s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}

@-webkit-keyframes stretchRight {
    0% {
        width: 9px;
    }

    10% {
        width: 10px;
    }

    20% {
        width: 11px;
    }

    30% {
        width: 12px;
    }

    40% {
        width: 13px;
    }

    50% {
        width: 14px;
    }

    60% {
        width: 13px;
    }

    70% {
        width: 12px;
    }

    80% {
        width: 11px;
    }

    90% {
        width: 10px;
    }

    100% {
        width: 9px;
    }
}

@keyframes stretchRight {
    0% {
        width: 9px;
    }

    10% {
        width: 10px;
    }

    20% {
        width: 11px;
    }

    30% {
        width: 12px;
    }

    40% {
        width: 13px;
    }

    50% {
        width: 14px;
    }

    60% {
        width: 13px;
    }

    70% {
        width: 12px;
    }

    80% {
        width: 11px;
    }

    90% {
        width: 10px;
    }

    100% {
        width: 9px;
    }
}

.detail-caret-left:hover {
    cursor: pointer !important;
    text-align: left;
    -webkit-animation-name: stretchLeft;
            animation-name: stretchLeft;
    -webkit-animation-duration: 1.2s;
            animation-duration: 1.2s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
}

@-webkit-keyframes stretchLeft {
    0% {
        width: 9px;
    }

    10% {
        width: 10px;
    }

    20% {
        width: 11px;
    }

    30% {
        width: 12px;
    }

    40% {
        width: 13px;
    }

    50% {
        width: 14px;
    }

    60% {
        width: 13px;
    }

    70% {
        width: 12px;
    }

    80% {
        width: 11px;
    }

    90% {
        width: 10px;
    }

    100% {
        width: 9px;
    }
}

@keyframes stretchLeft {
    0% {
        width: 9px;
    }

    10% {
        width: 10px;
    }

    20% {
        width: 11px;
    }

    30% {
        width: 12px;
    }

    40% {
        width: 13px;
    }

    50% {
        width: 14px;
    }

    60% {
        width: 13px;
    }

    70% {
        width: 12px;
    }

    80% {
        width: 11px;
    }

    90% {
        width: 10px;
    }

    100% {
        width: 9px;
    }
}

.slideout {
    width: 0;
    padding: 10px 0 !important;
    transition: width, .5s linear;
    border-left: 1px solid #ccc;
}

.fade-in {
    opacity: 0;
